import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NotificationService } from './services/notification.service';
import { TnFormErrorService } from './services/tn-form-error.service';
import { CapitalizePipe } from './../@core/pipes/capitalize.pipe';
import { TnFileUploadComponent } from './tn-file-upload/tn-file-upload.component';
import { PagerService } from './services/pagination';
import { ConfirmDialogComponent } from './confirm-dialog/confirm-dialog.component';
import { MatDialogModule, MatButtonModule } from '@angular/material';
import { MaterialModule } from '../core-module/material.module';
import {NgxMaterialTimepickerModule} from 'ngx-material-timepicker';
import { MomentFormatPipe } from './pipes/momentformat.pipe';
import { ControlMessagesComponent } from './control-messages/control-messages.component';
import { ValidationService } from './utils/validationMessage';
import { OnlyNumber } from './directives/onlynumber';
import { RemoveHtmlTag } from './pipes/removehtmltag.pipe';
import { StatusDialogComponent } from './status-dialog/status-dialog.component';

@NgModule({
  imports: [
    CommonModule,
    MatDialogModule,
    MatButtonModule,
    FormsModule,
    ReactiveFormsModule,
    MaterialModule,
    NgxMaterialTimepickerModule
  ],
  declarations: [
    CapitalizePipe,
    MomentFormatPipe,
    TnFileUploadComponent,
    ConfirmDialogComponent,
    ControlMessagesComponent,
    OnlyNumber,
    RemoveHtmlTag,
    StatusDialogComponent
  ],
  entryComponents: [
    ConfirmDialogComponent,
    StatusDialogComponent
  ],
  exports: [
    CapitalizePipe,
    MomentFormatPipe,
    TnFileUploadComponent,
    ConfirmDialogComponent,
  
    ControlMessagesComponent,
    OnlyNumber,
    RemoveHtmlTag,
    StatusDialogComponent
  ],
  providers: [
    ValidationService,
    TnFormErrorService,
    NotificationService,
  ],
})
export class CoreModule {
  static forChild(): ModuleWithProviders {
    return  {
      ngModule: CoreModule,
      providers: [
        TnFormErrorService,
        ValidationService,
        NotificationService,
        PagerService
      ],
    } as ModuleWithProviders;
  }
}
